<template>
<div class="container-fluid px-1 py-5 mx-auto">
    <div class="row d-flex justify-content-center">
        <div class="col-xl-7 col-lg-8 col-md-9 col-11 text-center">
            <div class="card">
                <h3 class="text-center mb-4">User Registeration form</h3>
                <form class="form-card" v-on:submit.prevent="sub">
                    <div class="row justify-content-between text-left">
                        <div class="form-group col-sm-6 flex-column d-flex"> <label class="form-control-label px-3">Name<span class="text-danger"> *</span></label> <input v-model="user_data.name" type="text" id="name" name="name" placeholder="Enter your name" required> </div>
                        <div class="form-group col-sm-6 flex-column d-flex"> <label class="form-control-label px-3">Email<span class="text-danger"> *</span></label> <input v-model="user_data.email" required type="text" id="email" name="email" placeholder="Enter yor email"> </div>
                    </div>
                    <div class="row justify-content-between text-left">
                        <div class="form-group col-sm-6 flex-column d-flex"> <label class="form-control-label px-3">Date of Birth<span class="text-danger"> *</span></label> <input v-model="user_data.dob" required type="date" id="dob" name="dob" placeholder="D.O.B." > </div>
                        <div class="form-group col-sm-6 flex-column d-flex"> <label class="form-control-label px-3">Phone number<span class="text-danger"> *</span></label> <input required type="text" id="phone" name="phone" v-model="user_data.phone" placeholder="Enter your Phone no." > </div>
                    </div>
                    
                    <div class="row justify-content-center">
                        <div class="form-group col-sm-6"> <button type="submit" class="btn-block btn-primary">Submit</button> </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>
<style>
body{color: #000;overflow-x: hidden;height: 100%;background-image: url("https://i.imgur.com/GMmCQHC.png");background-repeat: no-repeat;background-size: 100% 100%}.card{padding: 30px 40px;margin-top: 60px;margin-bottom: 60px;border: none !important;box-shadow: 0 6px 12px 0 rgba(0,0,0,0.2)}.blue-text{color: #00BCD4}.form-control-label{margin-bottom: 0}input, textarea, button{padding: 8px 15px;border-radius: 5px !important;margin: 5px 0px;box-sizing: border-box;border: 1px solid #ccc;font-size: 18px !important;font-weight: 300}input:focus, textarea:focus{-moz-box-shadow: none !important;-webkit-box-shadow: none !important;box-shadow: none !important;border: 1px solid #00BCD4;outline-width: 0;font-weight: 400}.btn-block{text-transform: uppercase;font-size: 15px !important;font-weight: 400;height: 43px;cursor: pointer}.btn-block:hover{color: #fff !important}button:focus{-moz-box-shadow: none !important;-webkit-box-shadow: none !important;box-shadow: none !important;outline-width: 0}
</style>
<script>
import axios from 'axios'

export default{
    data(){
        return {
            user_data:
            {
                name:"",
                email:"",
                phone:"",
                dob:""
            }
        }
    },
    methods:{
        sub:async function(){
            console.log(this.user_data);
            var dob=this.user_data.dob.replace(/-/g,"/");
            var x=new Date(dob);
            var currentDate=new Date().toJSON().slice(0,10);
            var myAge=~~((Date.now(currentDate)-x)/(31557600000));
            if(myAge<18){
                alert("You are not above 18");
                return;
            }
            const response=await axios.post(
                "/user",this.user_data
            );
            console.log(response.data);
            if(response.data.message=="invalid phone"){
                alert("enter valid phone number!!!");
                return;
            }
            if(response.data.status=="success"){
                this.$router.push({
                    path:'/second'
                });
                return;
            }
            return;
        },
    }

}
</script>